* {
  box-sizing: border-box
  
} 
body {
    font: 4vmin "Bitter", Futura, sans-serif;
    margin: 4vmin;
    text-align: center;
    background-image: url("detective.jpg");
  }
h1{
  margin-top: 2vmin;
  margin-bottom: 1vmin;
  text-align: center;
}

.button{
  margin:auto;
  width: 50vmin;
  height: 8vmin;
  border-radius: 1vmin;
  font-size: 4vmin;
  color: white;
  border: none;
  background-color: #f44336;
}

form{
  margin: auto  ;
  width: 50vmin;
  text-align: center;
}

form >label>*{
  
  height: 8vmin;
  border: lightgray;
  border-width: 0.1vmin;
  border-style: solid;
  border-radius: 1vmin;
  margin: auto;
  margin-top: 1vh;
  margin-bottom: 1vh;
  width: 50vmin;
  font-size: 4vmin;
  text-align: center;
  
}

.content{
  height: 8vmin;
  border: lightgray;
  border-width: 0.1vmin;
  border-style: solid;
  border-radius: 1vmin;
  margin: auto;
  margin-top: 1vh;
  margin-bottom: 1vh;
  width: 50vmin;
  font-size: 4vmin;
  text-align: center;
}

#container{
  width: 70vw;  
  
  padding: 5vmin;
  margin: auto;
  background-color: white;
  align-items: center;

}

label{
  

  height:8vmin;
}

p{
  margin: 1vmin;
  padding: 0;
  font-size: 3vmin;
}

Tile{
  
  float:left; 
}

table{
  margin: auto;
}

.tile{
  width: 100%;
  height: 100%;
  display: inline-block;
  font-size: 1.5vw;
  word-wrap: break-word;
}
tr{
  width: 100%;
}